.wrap {
  width: 354px;
  margin: 0 auto;
}

.main > div > h1 {
  font-size: 2.4rem;
}

.backToRoot {
  padding: 1rem 1.2rem;
  min-width: 8rem;
  text-align: center;
  margin-top: 1rem;
  border-radius: 100px;
  border: none;
  text-decoration: none;
  color: white;
  cursor: pointer;
  transition: 0.3s all ease-in;
  background-color: var(--primary-color);
}

.backToRoot:hover {
  background-color: #254dd1;
}
